.background-top-bar{
	background-color: rgb(23, 26, 45);
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	position: fixed;
	left:0px;
	right:0px;
	top:0px;
	height: 100vh;
	z-index: 10;
	-webkit-transition: all 2s;
	-o-transition: all 2s;
	transition: all 2s;
}

.background-top-bar.on{
	height: 100px;
}

.button-pos{
	position: fixed;
	right:30px;
	top:30px;
	display: -ms-inline-flexbox;
	display: inline-flex;
}

.page-padding{
	top:0px;
	left:0px;
	right:0px;
	height: 100px;
}


